.profileView {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.profileVideo {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: 0;
}

.profileVideoText {
    position: relative;
    z-index: 1;
    color: white;
    top: 55%;
    margin-left: 3%;
    padding: 0 1rem;
}

.profileVideoText h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.profileVideoText p {
    font-size: 1.3rem;
    max-width: 50%;
}

.profileVideoText .buttonsDiv button {
    color: white;
    border: none;
    font-size: 1.1rem;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 30px;
    padding-right: 30px;
    cursor: pointer;
    border-radius: 5px;
    box-shadow: 0 0 10px 10px rgba(28, 28, 28, 0.092);
    margin-right: 10px;
}

.profileVideoText p b {
    color: rgb(238, 173, 45);
}

#resume {
    background-color: rgb(49, 49, 49);
}

#linkedin {
    background-color: rgb(10, 102, 194);
}

.profileHeader {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: white;
    padding: 1.5rem;
    position: fixed;
    z-index: 1;
    width: 100%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.7) 70%, rgba(0, 0, 0, 0.6) 80%, rgba(0, 0, 0, 0.4) 90%, rgba(0, 0, 0, 0) 100%);
}

.logoHeader{
    height: 1.7rem;
}

.changeProfile {
    height: 2rem;
    width: auto;
    border-radius: 200px;
    border: 1px solid white;
    cursor: pointer;
}

.menuOptions {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 2rem;
}

.moreAboutMeContainer {
    background-color: rgb(20, 20, 25);
    color: white;
    padding: 3rem;
}

.moreAboutMeContainer h2 {
    margin-top: 0px;
}

.forYouContent {
    display: flex;
}

.item {
    margin-right: 1rem;
}

.itemImg {
    /* border: 1px solid white; */
    width: 300px;
    height: 168.75px;
}